body {
  /* background-color: #EAD2AC; */
  width: 100vw;
  height: 100vh;
  background-image: linear-gradient(147deg, #fe8a39 0%, #fd3838 74%);
  background-repeat: no-repeat;
}

.container {
  border: solid 20px black;
  border-radius: 10px;
  padding-left: 30px;
  padding-right: 30px;
}

.hidden {
  display: none;
}

.webcam {
  position: absolute;
  margin: 10px;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 9;
  width: 640;
  height: 480;
}

.canvas {
  position: absolute;
  margin: 10px;
  left: 0;
  right: 0;
  text-align: center;
  z-index: 9;
  width: 640;
  height: 480;
}

.colContainer {
  margin-top: 150px;
  width: 100%;
  display: flex;
  justify-content: center;
  justify-self: center;
}

.col {
  display: flex;
  flex-direction: column;
  justify-content: end;
  text-align: center;
  width: 40px;
  height: 1px;
  margin: 10px;
  background-image: linear-gradient(147deg, #fe8a39 0%, #fd3838 74%);
  transition: max-height 0.25s ease-in;
}

.blog-slider {
  height: 90%;
  min-height: 650px;
  position: relative;
  min-width: 360px;
  max-width: 750px;
  margin-left: auto;
  background: #fff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 25px;
  border-radius: 25px;
  height: 400px;
  transition: all 0.3s;
  display: flex;
  flex-direction: column;
}

.toggleContainer {
  display: flex;
  justify-content: center;
  margin-top: 75px;
}

.toggleContainer>p {
  margin: auto;
}

.toggler {
  margin: 20px;
  display: flex;
  flex-direction: column;
}

input[type=checkbox] {
  height: 0;
  width: 0;
  visibility: hidden;
}

label {
  cursor: pointer;
  text-indent: -9999px;
  width: 100px;
  height: 50px;
  background-image: linear-gradient(147deg, #fe8a39 0%, #fd3838 74%);
  display: block;
  border-radius: 100px;
  position: relative;
}

label:after {
  content: '';
  position: absolute;
  top: 5px;
  left: 5px;
  width: 40px;
  height: 40px;
  margin: auto;
  background: #fff;
  border-radius: 90px;
  transition: 0.3s;
}

input:checked+label {
  background-image: linear-gradient(147deg, #fe8a39 0%, #fd3838 74%);
}

input:checked+label:after {
  left: calc(100% - 5px);
  transform: translateX(-100%);
}

label:active:after {
  width: 50px;
}